document.addEventListener('DOMContentLoaded', function () {
    iniciarApp();
});

function iniciarApp() {
    navegacionFija();
    crearGaleria();
    scrollNav();
}

function navegacionFija() {
    const barra = document.querySelector('.header');
    const sobreFestival = document.querySelector('.sobre-festival');
    const body = document.querySelector('body');

    window.addEventListener('scroll', function () {
        if (sobreFestival.getBoundingClientRect().bottom < 0) {
            barra.classList.add('fijo');
            body.classList.add('body-scroll');
        } else {
            barra.classList.remove('fijo');
            body.classList.remove('body-scroll');
        }
    });
}

function scrollNav() {
    const enlaces = document.querySelectorAll('.navegacion-principal a'); //seleccionamos todo lo enlaces a 
    enlaces.forEach(enlace => { // iteramos en cada uno 
        enlace.addEventListener('click', function (e) { // se escucha un evento del clic
            e.preventDefault(); // denegamos el default 

            const seccionScroll = e.target.attributes.href.value; //guardamos el href del enlace 
            const seccion = document.querySelector(seccionScroll); //pasamos el seccion 
            seccion.scrollIntoView({ // atributos de la api
                behavior: "smooth"
            });
        })
    });
}


function crearGaleria() {
    //seleccionamos de el html la class .galeria-imagenes
    const galeria = document.querySelector('.galeria-imagenes');

    //con el for iteramos cada una de las fotos y lo pasamos a los selectores de cada imagen ${i}
    for (let i = 1; i <= 12; i++) {
        // creamos un elemento picture
        const imagen = document.createElement('picture');
        imagen.innerHTML = `
        <source srcset="build/img/thumb/${i}.avif" type="imagen/avif">
        <source srcset="build/img/thumb/${i}.webp" type="imagen/webp">
        <img loading="lazy" width="300" height="200" src="build/img/thumb/${i}.jpg"
        alt="imagen galeria de artistas">
        `;
        // a la galeria le pasamos la cont imagen 
        galeria.appendChild(imagen);
        //pasarle parametros como colback para que defina o escuche a cual imagen se hace click
        imagen.onclick = function () {
            mostrarImagen(i);
        }

    }
}

function mostrarImagen(id) {

    const imagen = document.createElement('picture');
    imagen.innerHTML = `
    <source srcset="build/img/grande/${id}.avif" type="imagen/avif">
    <source srcset="build/img/grande/${id}.webp" type="imagen/webp">
    <img loading="lazy" width="300" height="200" src="build/img/grande/${id}.jpg"
    alt="imagen galeria de artistas">
    `;

    //crea el overlay con la imagen 
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay');
    //para cerrar modal cuando se toque en cualquier parte de la pantalla 
    overlay.onclick = function () {
        const body = document.querySelector('body');
        overlay.classList.add('fijar-body');
        overlay.remove(); // remove es para cerrar el modal 

    }

    //boton cerra modal 
    const cerraModal = document.createElement('P');
    cerraModal.textContent = 'X';
    cerraModal.classList.add('btn-cerrar'); //scss
    cerraModal.onclick = function () {
        const body = document.querySelector('body');
        body.classList.add('fijar-body');
        overlay.remove(); // remove es para cerrar el modal 

    }
    overlay.appendChild(cerraModal);

    // a√±ardirlo al HTML 
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body');
}